@import '../__variables';

.bPlDashboardFeatureCompare {
	width: 100%;
	max-width: 1440px;
	margin: 0 auto;
	overflow-x: auto;

	.featureComparePricing {
		width: 100%;
		min-width: 1380px;
		display: flex;
		align-items: center;
		gap: 26px;
		padding-bottom: 32px;
		border-bottom: 1px solid #E4E4E4;
		margin-bottom: 32px;
		flex-wrap: wrap;

		.cyclesSelector {
			margin-right: auto;

			h2 {
				font-size: 18px;
				font-weight: 400;
				color: var(--bpl-dashboard-title-color, $titleColor);
				margin: 0 0 21px 0;
			}

			.cycles {
				display: inline-flex;
				align-items: center;
				justify-content: center;
				background: $whiteColor;
				padding: 6px;
				border: 1px solid #e4e4e4;
				border-radius: 10px;
				overflow: hidden;

				button {
					font-size: 16px;
					font-weight: 500;
					color: var(--bpl-dashboard-title-color, $titleColor);
					background-color: #0000;
					padding: 8px 15px;
					border-radius: 5px;
					border: 0;
					cursor: pointer;

					&.active {
						color: $whiteColor;
						background: linear-gradient(to bottom, rgba($whiteColor, 0.2), rgba($whiteColor, 0.07)), var(--bpl-dashboard-secondary-color, $secondaryColor);
						box-shadow: inset 0 0 0 1px rgba($whiteColor, 0.26), 0 0 0 1.5px var(--bpl-dashboard-secondary-color, $secondaryColor), 0 0 0 5px #0000000a;
					}
				}
			}

			h2.pricingTitle {
				font-size: 16px;
				font-weight: 500;
				display: inline-flex;
				align-items: center;
				justify-content: center;
				color: $whiteColor;
				background: linear-gradient(to bottom, rgba($whiteColor, 0.2) 0%, rgba($whiteColor, 0.07) 100%), var(--bpl-dashboard-secondary-color, $secondaryColor);
				padding: 10px 20px;
				border-radius: 10px;
				box-shadow: inset 0 0 0 1px rgba($whiteColor, 0.26), 0 0 0 1.5px var(--bpl-dashboard-secondary-color, $secondaryColor), 0 0 0 5px #0000000a;
			}
		}

		.plan {
			min-width: 310px;
			max-width: 100%;
			padding: 18px 24px;
			border-radius: 10px;

			.note {
				font-size: 14px;
				font-weight: 400;
			}

			.price {
				font-size: 28px;
				font-weight: 500;
				margin-bottom: 6px;
				display: flex;
				align-items: baseline;
				gap: 10px;

				h3 {
					font-size: 16px;
					font-weight: 500;
					margin: 0;
				}
			}

			.bPlButton {
				width: 100%;
				font-size: 14px;
				font-weight: 500;
				justify-content: center;
				padding: 10px 20px;
				margin-top: 14px;

				&:hover {
					color: #fff !important;
				}
			}

			&.free {
				background: $whiteColor;
				border: 1px solid #485e9033;

				h3 {
					color: var(--bpl-dashboard-content-color, $contentColor);
				}

				.price {
					color: var(--bpl-dashboard-title-color, $titleColor);
				}

				.note {
					color: var(--bpl-dashboard-title-color, $titleColor);
				}

				.bPlButton {
					color: var(--bpl-dashboard-title-color, $titleColor);
					background: #0000;
					border: 1px solid #DADFE9;

					&::before {
						background: var(--bpl-dashboard-primary-color, $primaryColor);
					}
				}
			}

			&.premium {
				background: var(--bpl-dashboard-primary-color, $primaryColor);
				border: 1px solid var(--bpl-dashboard-primary-color, $primaryColor);

				h3,
				.price,
				.note {
					color: $whiteColor;
				}

				.bPlButton {
					color: var(--bpl-dashboard-title-color, $titleColor);
					background: $whiteColor;

					&::before {
						background: var(--bpl-dashboard-secondary-color, $secondaryColor);
					}
				}
			}
		}
	}

	table {
		width: 100%;
		min-width: 1380px;
		text-indent: 0;
		border-color: inherit;
		border-collapse: collapse;

		thead {
			tr {
				font-size: 24px;
				font-weight: 500;
				text-align: left;
				color: var(--bpl-dashboard-title-color, $titleColor);
				display: inline-block;
				margin-bottom: 20px;

				&:not(:first-child) {
					min-width: 310px;
					max-width: 100%;
				}
			}
		}

		tbody {
			tr {
				display: flex;
				align-items: stretch;
				gap: 26px;
				margin-bottom: 20px;

				&:last-child {
					border-bottom: 0;
				}
			}

			td {
				padding: 0 0 20px 0;
				border-bottom: 1px solid #E4E4E4;

				&:first-child {
					flex: 1;
					font-size: 18px;
					font-weight: 400;
					color: var(--bpl-dashboard-content-color, $contentColor);
					margin-right: auto;
				}

				&:not(:first-child) {
					min-width: 310px;
					max-width: 100%;
					text-align: center;
				}

				>* {
					display: inline-block;
				}

				svg {
					fill: currentColor;
				}

				.icon {
					width: 24px;
					height: 24px;
					color: $whiteColor;
					display: inline-flex;
					align-items: center;
					justify-content: center;
					border-radius: 50%;
				}

				.icon.check {
					background: #34C759;

					svg {
						width: 20px;
						height: 20px;
					}
				}

				.icon.cross {
					background: #FF383C;

					svg {
						width: 18px;
						height: 18px;
					}
				}
			}
		}
	}
}