@import url('https://fonts.googleapis.com/css2?family=Lato:wght@700;900&family=Roboto:wght@100..900&display=swap');

@import '__variables.scss';

.notice,
.fs-notice,
.fs-has-title,
.fs_notice_board {
	width: 0;
	height: 0;
	position: absolute;
	top: -9999px;
	left: -9999px;
	opacity: 0;
	visibility: hidden;
	display: none !important;
	padding: 0 !important;
	margin: 0 !important;
}

.bPlDashboard {
	font-family: "Roboto", sans-serif;
	width: calc(100% + 20px);
	max-width: calc(100% + 20px);
	min-height: 100vh;
	background: #f6f8fa;
	margin: 0 -20px 0 -20px;

	* {
		font-family: "Roboto", sans-serif;
		padding: 0;
		margin: 0;
		box-sizing: border-box;
		line-height: 1.5;
	}

	.bPlDashboardContainer {
		width: 100%;
		max-width: 1320px;
		padding: 0 15px;
		margin: 0 auto;
	}

	.bPlDashboardMain {
		padding: 52px 32px;

		.bPlDashboardContainer {
			display: flex;
			flex-direction: column;
			gap: 35px;
			padding: 20px 0;
		}
	}

	.bPlDashboardCard {
		background-color: $whiteColor;
		padding: 24px;
		border-radius: 12px;
	}

	.bPlDashboardButtons {
		display: flex;
		align-items: center;
		gap: 10px;
	}
}