@import '../__variables';

.bPlDashboardDemos {
	width: 100%;
	max-width: 1440px;
	min-height: 600px;
	height: calc(100dvh - 127px - var(--wp-admin--admin-bar--height, 32px));
	background: $whiteColor;
	display: flex;
	border-radius: 10px;
	overflow: hidden;
	padding: 0 !important;
	margin: 0 auto;

	.bPlDashboardButtons {
		gap: 13px;
		flex-wrap: wrap;

		.bPlButton {
			font-size: 14px;
			font-weight: 500;
			color: $whiteColor;
			padding: 8px 20px;
			border-radius: 5px;

			&:first-child {
				background: var(--bpl-dashboard-primary-color, $primaryColor);

				&::before {
					background: var(--bpl-dashboard-secondary-color, $secondaryColor);
				}
			}

			&:last-child {
				background: var(--bpl-dashboard-secondary-color, $secondaryColor);

				&::before {
					background: var(--bpl-dashboard-primary-color, $primaryColor);
				}
			}
		}
	}

	.sidebar {
		min-width: 350px;
		background: #fafbfc;
		display: flex;
		flex-direction: column;
		border: 1px solid #E3E6EE;
		border-radius: 10px 0 0 10px;

		.sidebarHeader {
			padding: 26px 21px;

			p {
				font-size: 16px;
				color: rgba(var(--bpl-dashboard-content-color-rgb, $contentColor), .9);
				margin: 0 0 4px 0;
			}

			.search {
				border-radius: 5px;
				position: relative;
				max-width: 100%;

				svg {
					width: 22px;
					height: 22px;
					position: absolute;
					top: 50%;
					left: 16px;
					fill: rgba(var(--bpl-dashboard-title-color-rgb, $titleColor), .75);
					transform: translateY(-50%);
				}

				input {
					height: 45px;
					width: 100%;
					color: rgba(var(--bpl-dashboard-content-color-rgb, $contentColor), .75);
					background: $whiteColor;
					padding-left: 48px;
					border: 1px solid rgba(var(--bpl-dashboard-content-color-rgb, $contentColor), .2);
					border-radius: 5px;
					outline: 0;
					box-shadow: none;

					&:hover,
					&:focus,
					&:active {
						border: 1px solid rgba(var(--bpl-dashboard-content-color-rgb, $contentColor), .2);
						outline: 0;
						box-shadow: none;
					}
				}

				input::placeholder {
					color: rgba(var(--bpl-dashboard-content-color-rgb, $contentColor), .75);
				}
			}

			.bPlDashboardButtons {
				display: none;
			}
		}

		.sidebarList {
			flex: 1;
			display: flex;
			flex-direction: column;
			overflow: auto;

			.parentDemo {
				width: 100%;
				color: var(--bpl-dashboard-title-color, $titleColor) !important;
				background: $whiteColor !important;
				padding: 16px;
				display: flex;
				align-items: center;
				gap: 10px;
				border-radius: 0;
				border: none;
				margin: 0;
				cursor: pointer;

				.icon {
					display: inline-flex;
					align-items: center;
					justify-content: center;
				}

				svg {
					width: 24px;
					height: 24px;
					fill: rgba(var(--bpl-dashboard-title-color-rgb, $titleColor), .75);

					&:last-child {
						width: 26px;
						height: 26px;
					}

					&:last-child {
						width: 21px;
						height: 21px;
						margin-left: auto;
					}
				}

				&.active {
					color: var(--bpl-dashboard-primary-color, $primaryColor) !important;

					svg {
						fill: var(--bpl-dashboard-primary-color, $primaryColor);

						&.angelDown {
							transform: rotate(180deg);
						}
					}
				}
			}

			.demoItem {
				border-bottom: .5px solid #485e9033;

				.accordion {
					ul {
						// max-height: 0;
						list-style: none;
						transition: all 300ms ease-in-out;
						padding: 0 0 0 20px;
						border-left: 1px solid rgba(var(--bpl-dashboard-title-color-rgb, $titleColor), .75);
						margin: 0 0 0 16px;
						margin: 10px 0 16px 16px;

						li {
							font-size: 14px;
							position: relative;
							color: rgba(var(--bpl-dashboard-title-color-rgb, $titleColor), .75);
							background: transparent;
							border-radius: 5px;
							transition: all 300ms ease-in-out;
							padding-left: 22px;
							cursor: pointer;

							&::before {
								content: '';
								width: 6px;
								height: 6px;
								position: absolute;
								top: 50%;
								left: 7px;
								transform: translateY(-50%);
								background: currentColor;
								border-radius: 50%;
							}

							&:not(:last-child) {
								margin-bottom: 5px;
							}

							&.active {
								color: var(--bpl-dashboard-primary-color, $primaryColor);
								background: $whiteColor;
								padding: 8px 0 8px 22px;
							}
						}
					}

					&.expanded {
						ul {
							// max-height: 500px;
							overflow-y: auto;
							// margin: 10px 0 16px 16px;
						}
					}
				}
			}

			.notFound {
				display: flex;
				align-items: center;
				justify-content: center;
				gap: 10px;

				svg {
					width: 24px;
					height: 24px;
				}
			}
		}

		.sidebarList::-webkit-scrollbar {
			width: 5px;
			height: 10px;
			background-color: #0000;
		}

		.sidebarList::-webkit-scrollbar-track {
			border-radius: 5px;
			background-color: #0000;
		}

		.sidebarList::-webkit-scrollbar-thumb {
			border-radius: 5px;
			background-color: #b9b9b9;
		}
	}

	.main {
		flex: 1 1 0%;
		display: flex;
		flex-direction: column;
		overflow: hidden;
		background: $whiteColor;

		.mainHeader {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 28px 28px 15px 28px;
			border-bottom: 1px solid #DADFE9;

			.headerInfo {
				h3 {
					font-size: 20px;
					font-weight: 600;
					color: var(--bpl-dashboard-title-color, $titleColor);
					margin: 0;
				}
			}
		}

		.canvas {
			flex: 1 1 0%;
			background: $whiteColor;
			display: flex;
			flex-direction: column;
			position: relative;
			border: none;
			border-radius: 0;
			overflow: hidden;

			.bPlLoading {
				position: absolute;
				inset: 0;
				background-color: rgba(255, 255, 255, 0.75);
				display: flex;
				align-items: center;
				justify-content: center;
				z-index: 10;
			}

			iframe {
				flex: 1 1 0%;
				width: 100%;
				height: 100%;
				vertical-align: middle;
				border: none;
			}

			.canvasImg {
				text-align: center;
				width: calc(100% - 40px);
				height: calc(100% - 40px);
				margin: auto;
				overflow: hidden;

				img {
					max-width: 100%;
					transition: all 1s ease;
				}
			}
		}
	}
}

@media only screen and (max-width: 1024px) {
	.bPlDashboardDemos {
		flex-direction: column;

		.sidebar {
			width: 100%;
			padding-bottom: 0;

			.sidebarHeader {
				display: flex;
				justify-content: space-between;
				flex-wrap: wrap;
				gap: 15px;

				.bPlDashboardButtons {
					display: flex;
				}
			}

			.sidebarList {
				max-width: 100%;
				flex-direction: row;
				flex-wrap: nowrap;
				padding: 0 8px 16px 8px;

				button {
					width: auto;
					min-width: 200px;
					align-items: center;
					padding: 12px 16px;

					.texts {
						h4 {
							margin: 0;
						}

						p {
							display: none;
						}

						.category {
							display: none;
						}
					}

					.externalIcon {
						display: none;
					}
				}
			}
		}

		.main {
			width: 100%;

			.mainHeader {
				display: none;
			}
		}
	}
}