@import '../__variables.scss';

.bPlDashboardActivation {
	max-width: 1200px;
	padding: 0 0 32px 0 !important;
	margin: 0 auto;

	// Header Section
	.activationHeader {
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex-wrap: wrap;
		gap: 20px;
		padding: 24px 24px 28px 24px;
		border-bottom: 1px solid #485E9033;
		margin-bottom: 35px;

		.pluginInfo {
			display: flex;
			align-items: center;
			gap: 20px;

			img {
				width: 60px;
				height: 60px;
				border-radius: 10px;
			}

			.pluginDetails {
				h1 {
					font-size: 24px;
					font-weight: 700;
					color: var(--bpl-dashboard-title-color, $titleColor);
					margin: 0 0 3px 0;
				}

				.version {
					font-size: 14px;
					font-weight: 500;
					color: var(--bpl-dashboard-content-color, $contentColor);
					margin: 0;
				}
			}
		}

		.statusBadge {
			font-size: 12px;
			font-weight: 600;
			padding: 8px 20px;
			border-radius: 8px;

			&.active {
				color: $whiteColor;
				background: #34C759;
			}

			&.inactive {
				color: $whiteColor;
				background: #FF383C;
			}
		}
	}

	// Content Section
	.activationContent {
		max-width: 960px;
		padding: 0;
		margin: 0 auto;
	}

	// Loading State
	.activationLoading {
		text-align: center;
		padding: 80px 20px;

		p {
			font-size: 16px;
			color: var(--bpl-dashboard-content-color, $contentColor);
			margin: 0;
		}
	}

	// Success State
	.activationSuccess {
		text-align: center;
		padding: 0 20px;

		.successIcon {
			width: 100px;
			height: 100px;
			display: flex;
			align-items: center;
			justify-content: center;
			background: url('data:image/svg+xml;base64,');
			background-size: contain;
			background-position: center center;
			background-repeat: no-repeat;
			margin: 0 auto 22px auto;

			svg {
				fill: #34C759;
				width: 60px;
				height: 60px;
			}
		}

		h2 {
			font-size: 48px;
			font-weight: 700;
			color: var(--bpl-dashboard-title-color, $titleColor);
			margin: 0 0 10px 0;
		}

		.successMessage {
			font-size: 16px;
			color: rgba(var(--bpl-dashboard-content-color-rgb, $contentColor), 0.7);
			margin: 0 0 42px 0;
		}

		.licenseDisplay {
			position: relative;
			max-width: 500px;
			margin: 0 auto 20px auto;

			.licenseInput {
				width: 100%;
				font-size: 16px;
				color: var(--bpl-dashboard-content-color, $contentColor);
				background: $whiteColor;
				padding: 14px 76px 14px 33px;
				border: 1px solid #485E9088;
				border-radius: 10px;

				&:focus {
					outline: none;
					border-color: var(--bpl-dashboard-primary-color, $primaryColor);
				}
			}

			.toggleVisibility {
				position: absolute;
				right: 15px;
				top: 50%;
				transform: translateY(-50%);
				background: none;
				border: none;
				cursor: pointer;
				padding: 5px;
				color: #A3AEC7;
				transition: color 0.2s;

				&:hover {
					color: var(--bpl-dashboard-primary-color, $primaryColor);
				}

				svg {
					width: 28px;
					height: 28px;
				}
			}
		}

		.licenseActions {
			display: flex;
			justify-content: center;
			gap: 20px;
			margin-top: 20px;
			flex-wrap: wrap;

			.linkButton {
				padding: 8px 18px;
				border: 1px solid #D1D5DB;
				background: $whiteColor;
				color: var(--bpl-dashboard-primary-color, $primaryColor);
				border-radius: 6px;
				font-size: 14px;
				font-weight: 500;
				cursor: pointer;
				transition: all 0.2s;

				&:hover {
					border-color: var(--bpl-dashboard-primary-color, $primaryColor);
					background: #F0F4FF;
				}

				&.danger {
					color: #D32F2F;
					border-color: #D32F2F;

					&:hover {
						background: #FFEBEE;
					}
				}
			}
		}
	}

	// Activation Form
	.activationForm {
		padding: 0 20px;

		h2 {
			font-size: 28px;
			font-weight: 700;
			color: var(--bpl-dashboard-title-color, $titleColor);
			margin: 0 0 15px 0;
		}

		.formGroup {
			margin-bottom: 20px;

			.licenseInput {
				width: 100%;
				font-size: 15px;
				color: var(--bpl-dashboard-content-color, $contentColor);
				padding: 16px 25px;
				border: 1px solid #485E9088;
				border-radius: 4px;
				transition: all .3s ease;

				&::placeholder {
					color: var(--bpl-dashboard-content-color, $contentColor);
				}

				&:focus {
					outline: none;
					border-color: var(--bpl-dashboard-primary-color, $primaryColor);
					box-shadow: 0 0 0 3px rgba(var(--bpl-dashboard-primary-color-rgb, $primaryColor), 0.1);
				}

				&:disabled {
					background: #F3F4F6;
					cursor: not-allowed;
				}
			}
		}

		.formDescription {
			font-size: 14px;
			color: var(--bpl-dashboard-content-color, $contentColor);
			margin: 0 0 12px 0;

			a {
				font-weight: 500;
				color: var(--bpl-dashboard-primary-color, $primaryColor);
				text-decoration: none;
				transition: all 0.2s ease-in-out;

				&:hover {
					text-decoration: underline;
				}
			}
		}

		.formPermissions {
			list-style-type: none;
			margin-bottom: 20px;

			li {
				display: flex;
				align-items: center;
				gap: 12px;
				font-size: 16px;

				&:not(:last-child) {
					margin-bottom: 18px;
				}

				svg {
					width: 30px;
					height: 30px;
					fill: var(--bpl-dashboard-title-color, $titleColor);
				}

				h4 {
					font-size: 14px;
					display: flex;
					align-items: center;
					gap: 3px;
					color: var(--bpl-dashboard-title-color, $titleColor);
					margin: 0 0 5px 0;

					svg {
						width: 20px;
						height: 20px;
					}
				}

				p {
					font-size: 12px;
					color: rgba(var(--bpl-dashboard-content-color-rgb, $contentColor), 0.8);
					margin: 0;
				}
			}
		}

		.links {
			display: flex;
			align-items: center;
			gap: 24px;
			flex-wrap: wrap;

			a {
				font-size: 12px;
				font-weight: 600;
				text-decoration: none;
				color: var(--bpl-dashboard-primary-color, $primaryColor);
				transition: all 0.2s ease-in-out;

				&:hover {
					text-decoration: underline;
				}
			}
		}

		.errorMessage {
			background: #FEE2E2;
			border: 1px solid #FCA5A5;
			color: #991B1B;
			padding: 12px 16px;
			border-radius: 6px;
			margin-bottom: 20px;
			font-size: 14px;
		}

		.activateButton,
		.cancelButton {
			width: auto;
			font-size: 16px;
			background: var(--bpl-dashboard-primary-color, $primaryColor);
			padding: 12px 16px;
			border-radius: 4px;
			margin-bottom: 28px;

			&::before {
				background: var(--bpl-dashboard-secondary-color, $secondaryColor);
			}

			&:disabled {
				opacity: 0.6;
				cursor: not-allowed;
			}
		}

		.cancelButton {
			margin-left: 18px;
			background: #D32F2F;

			&:hover {
				background: #991B1B;
			}
		}
	}
}