@import '../__variables';

.bPlDashboardBlocksToast {
	position: fixed;
	bottom: 20px;
	right: 20px;
	padding: 16px 20px;
	border-radius: 8px;
	display: flex;
	align-items: center;
	gap: 12px;
	font-size: 14px;
	font-weight: 500;
	z-index: 1000;
	animation: toastSlideInUp 0.3s ease-out;
	box-shadow: 0 4px 12px #00000026;

	&.loading {
		background-color: #e3f2fd;
		color: var(--bpl-dashboard-primary-color, $primaryColor);

		.components-spinner {
			color: var(--bpl-dashboard-primary-color, $primaryColor);
			margin: 0;
		}
	}

	&.success {
		background-color: #e8f5e9;
		color: #2e7d32;

		.checkmark {
			display: inline-flex;
			align-items: center;
			justify-content: center;
			width: 20px;
			height: 20px;
			background-color: #2e7d32;
			color: white;
			border-radius: 50%;
			font-size: 12px;
			font-weight: bold;
		}
	}

	&.error {
		background-color: #ffebee;
		color: #c62828;

		.error-icon {
			display: inline-flex;
			align-items: center;
			justify-content: center;
			width: 20px;
			height: 20px;
			background-color: #c62828;
			color: white;
			border-radius: 50%;
			font-size: 12px;
			font-weight: bold;
		}
	}
}

@keyframes toastSlideInUp {
	from {
		opacity: 0;
		transform: translateY(20px);
	}

	to {
		opacity: 1;
		transform: translateY(0);
	}
}

.dashboardBlocks {
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(440px, 1fr));
	gap: 25px;
	padding: 0;
	margin: 0;

	.block {
		display: flex;
		align-items: center;
		gap: 10px;
		background: #FAFAFA;
		border-radius: 10px;
		padding: 12px 18px;
		border: 1.5px solid #E9E9E9;
		opacity: 1;
		transition: opacity 0.3s ease;
		position: relative;

		.blockTitle {
			flex-grow: 1;
			font-size: 16px;
			font-weight: 500;
			color: var(--bpl-dashboard-title-color, $titleColor);
			text-transform: capitalize;
			margin: 0px !important;
		}

		&.disabled {

			.icon,
			.blockTitle,
			.actionBtn,
			.toggleSwitch {
				opacity: 0.5;
			}

			// opacity: 0.5;
		}

		.icon {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 40px;
			height: 40px;
			background: rgba(var(--bpl-dashboard-primary-color-rgb, $primaryColor), 0.15);
			border-radius: 50px;

			svg {
				fill: var(--bpl-dashboard-primary-color, $primaryColor) !important;
				width: 20px;
				height: 20px;
			}
		}

		.name {
			display: flex;
			flex-direction: column;
			font-size: 16px;
			font-weight: 500;
			text-transform: capitalize;
			flex-grow: 1;
			color: var(--bpl-dashboard-secondary-color, $secondaryColor);
			margin-right: auto;

			>a {
				font-size: 12px;
				margin-top: 5px;
				color: var(--bpl-dashboard-primary-color, $primaryColor);
				text-decoration: none;
				cursor: pointer;

				&:hover {
					text-decoration: underline;
				}
			}
		}

		.actionBtn {
			width: 30px;
			height: 30px;
			display: flex;
			justify-content: center;
			align-items: center;
			border-radius: 6px;
			cursor: pointer;
			transition: background 0.3s ease;

			svg {
				width: 16px;
				height: 16px;
				fill: $whiteColor;
			}
		}

		.toggleSwitch {
			position: relative;
			display: inline-block;
			width: 34px;
			height: 20px;

			input {
				width: 0;
				height: 0;
				opacity: 0;
			}

			.slider {
				background-color: #ccc;
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				transition: 0.4s;
				border-radius: 20px;
				cursor: pointer;

				&:before {
					content: '';
					color: var(--bpl-dashboard-primary-color, $primaryColor);
					background-color: $whiteColor;
					position: absolute;
					width: 14px;
					height: 14px;
					bottom: 3px;
					left: 3px;
					transition: 0.4s;
					border-radius: 50%;
				}
			}

			input:checked+.slider {
				background-color: var(--bpl-dashboard-primary-color, $primaryColor);

				&:before {
					content: url("data:image/svg+xml,%3Csvg%20className%3D'check'%20xmlns%3D'http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg'%20viewBox%3D'0%20-960%20960%20960'%20fill%3D'currentColor'%3E%3Cpath%20d%3D'm382-354%20339-339q12-12%2028-12t28%2012q12%2012%2012%2028.5T777-636L410-268q-12%2012-28%2012t-28-12L182-440q-12-12-11.5-28.5T183-497q12-12%2028.5-12t28.5%2012l142%20143Z'%20%2F%3E%3C%2Fsvg%3E");
					transform: translateX(14px);
				}
			}

			input:disabled+.slider {
				opacity: 0.5;
			}
		}

		.blockBadge {
			font-size: 12px;
			position: absolute;
			/* text-transform: uppercase; */
			color: $whiteColor;
			background: var(--bpl-dashboard-primary-color, $primaryColor);
			top: 0;
			left: 20px;
			transform: translateY(-50%);
			padding: 0px 11px;
			border-radius: 9px;
			z-index: 999;
			font-weight: 500;
		}

		.blockProBadge {
			color: $whiteColor;
			background: #f2a80b;
			left: auto;
			right: 20px;
		}

		.blockRequiredBadge {
			color: $whiteColor;
			background: #d32f2f;
			left: auto;
			right: 70px;
			transform: translateY(-50%);
		}
	}
}

.bPlDashboardBlocks {
	width: 100%;
	max-width: 1440px;
	background: $whiteColor;
	border-radius: 10px;
	margin: 0 auto;
	position: relative;

	.blocksTop {
		display: flex;
		align-items: center;
		padding: 20px 30px;
		border-bottom: 1px solid #00000030;

		h2 {
			font-size: 24px;
			font-weight: 600;
			color: var(--bpl-dashboard-title-color, $titleColor);
			margin: 0 50px 0 0;
		}

		.blocksSearch {
			width: 280px;
			position: relative;

			input {
				width: 100%;
				height: 38px;
				padding: 0 38px;
				border: 1px solid rgba(var(--bpl-dashboard-primary-color-rgb, $primaryColor), 0.5);
				border-radius: 8px;

				&:focus {
					outline-color: var(--bpl-dashboard-primary-color, $primaryColor);
				}
			}

			>svg,
			span {
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
			}

			>svg {
				fill: var(--bpl-dashboard-primary-color, $primaryColor);
				width: 20px;
				height: 20px;
				left: 8px;
			}

			span {
				width: 38px;
				height: 38px;
				display: inline-flex;
				align-items: center;
				justify-content: center;
				right: 0;
				cursor: pointer;

				svg {
					width: 20px;
					height: 20px;
				}
			}
		}

		.bPlButton {
			padding: 10px 21px;
			border-radius: 5px;
		}

		.activeAllBtn {
			background: var(--bpl-dashboard-primary-color, $primaryColor);
			margin: 0 10px 0 auto;

			&::before {
				background: var(--bpl-dashboard-secondary-color, $secondaryColor);
			}
		}

		.deActiveAllBtn {
			background: var(--bpl-dashboard-secondary-color, $secondaryColor);

			&::before {
				background: var(--bpl-dashboard-primary-color, $primaryColor);
			}
		}
	}

	.blocksContent {
		padding: 30px;

		.blocksGroup {
			padding-bottom: 20px;
			border-bottom: 2px solid #f0f0f0;
			margin-bottom: 32px;

			.groupTitle {
				font-size: 18px;
				font-weight: 600;
				text-transform: capitalize;
				color: var(--bpl-dashboard-title-color, $titleColor);
				padding: 0;
				margin: 0 0 10px 0;
			}
		}
	}

	.noBlocksFound {
		text-align: center;
		color: red;
		padding: 30px;
		margin: 0;
	}
}