@import '../__variables';

.bPlDashboardProAds {
	width: 100%;
	display: flex;
	justify-content: center;
	flex-direction: column-reverse;
	flex-wrap: wrap-reverse;
	gap: 25px;

	>div {
		flex: 1;
	}

	h3 {
		font-size: 16px;
		font-weight: 600;
		color: var(--bpl-dashboard-content-color, $contentColor);
		margin-bottom: 21px;
	}

	ul {
		list-style-type: none;
		margin-bottom: 32px;
		min-width: 230px;

		li {
			font-size: 14px;
			font-weight: 400;
			color: var(--bpl-dashboard-content-color, $contentColor);
			margin-bottom: 0;
			display: flex;
			align-items: start;
			justify-content: start;
			gap: 6px;

			&:not(:last-child) {
				margin-bottom: 10px;
			}

			span {
				min-width: 18px;
				height: 18px;
				display: inline-flex;
				align-items: center;
				justify-content: center;
				background: linear-gradient(to top right, var(--bpl-dashboard-primary-color, $primaryColor), var(--bpl-dashboard-secondary-color, $secondaryColor));
				border-radius: 50%;
				margin-top: 1px;

				svg {
					fill: $whiteColor;
					width: 15px;
					height: 15px;
				}
			}
		}
	}

	.bPlButton {
		font-size: 16px;
		color: $whiteColor;
		background: var(--bpl-dashboard-secondary-color, $secondaryColor);
		padding: 10px 21px;

		&::before {
			background: var(--bpl-dashboard-primary-color, $primaryColor);
		}
	}

	figure {
		width: 100%;
		height: 260px;
		display: flex;
		align-items: center;
		justify-content: center;
		background: #DFDCFF;
		padding: 16px;
		border-radius: 18px;

		img {
			max-width: 100%;
			max-height: 100%;
			object-fit: contain;
		}
	}
}